html, body {
  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.4;
  font-weight: 300;
  color: $gray;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
  line-height: 1.1;
  margin-bottom: 0;
}

h1, .h1 {
  font-size: 1.6rem;
  @include tablet {
    font-size: 2.2rem;
  }
}

h2, .h2 {
  // text-transform: uppercase;
  font-size: 1.4rem;
  @include tablet {
    font-size: 1.6rem;
  }
}

h3, .h3 {
  font-size: 1.4rem;
  color: $brand;
}

h4, .h4 {
  font-size: 1.25rem;
}

b, strong {
  font-weight: bold;
}

i, em {
  font-style: italic;
}

ul,
ol {
  padding-left: 1rem;
  margin-bottom: 2rem;
}

ul {
  list-style-type: disc;
  &.inline {
    list-style-type: none;
    padding-left: 0;
    li {
      float: left;
      margin-right: 1rem;
      &:after {
        content: "";
        display: table;
        clear: both;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
}

ol {
  list-style-type: decimal;
}

a {
  color: $brand;
}

p {
  margin-bottom: 2rem;
}

// p + ul {
//   margin-top: -1rem;
// }

hr {
  margin: 3rem 0;
  border: 0;
  height: 1px;
  background-color: $mgray;
}