.box {
  text-align: center;
  border: 1px solid $mgray;
  color: $gray;
  text-shadow: none;
}

.box,
.light-callout {
  background-color: $lgray;
  background-image: linear-gradient(rgba(white, 0.1),rgba(black, 0.1));
}

.dark-callout {
  background-color: $gray;
  background-image: linear-gradient(rgba(white, 0.1),rgba(black, 0.1));
  * {
    color: $white;
  }
}

.brand-callout {
  background-color: $brand;
  * {
    color: $white;
  }
  a[href*="tel"] {
    color: $white; 
  }
  .btn-primary {
    color: $brand;
    background-color: $white;
  }
}

.brand-two-callout {
  background-color: $brand2;
  * {
    color: $white;
  }
  a[href*="tel"] {
    color: $white;
  }
  .btn-primary {
    color: $brand;
    background-color: $white;
  }
  select, option {
    color: $gray;
  }
}

.brand-three-callout {
  background-color: $brand3;
  * {
    color: $white;
  }
}

.brand-four-callout {
  background-color: $brand4;
  * {
    color: $white;
  }
}

.brand-five-callout {
  background-color: $brand5;
  * {
    color: $white;
  }
}

.pattern-callout {
  background-color: darken($lgray, 3%);
  background-image: linear-gradient(rgba(darken($lgray, 3%), 0.4), rgba(darken($lgray, 3%), 0.4)), url(../img/octiv-pattern--dark.svg);
  background-size: 300px;
  @include IE {
    background-image: linear-gradient(rgba(darken($lgray, 3%), 0.4), rgba(darken($lgray, 3%), 0.4));
  }
  .color-boxes {
    margin-bottom: 0;
  }
}